<template>
	<div class="wrapper">
		<div class="search-bar">
			<BaseSearchBar
				:data-for-filter="clients"
				:search-key="'username'"
				@onFilter="onFilterHandle"
			/>
		</div>
		<v-virtual-scroll
			class="virual-scroll"
			:items="clientsOrigin"
			:item-height="70"
			:min-height="'50vh'"
			:max-height="'100vh'"
		>
			<template #default="{ item }">
				<div class="list_item">
					<div class="list_item__block_info">
						<v-avatar :color="getColor(item.username)">
							<div>{{ getFirstLetter(item.username) }}</div>
						</v-avatar>
						<div class="block_info__name">
							{{ item.username }}
						</div>
					</div>
					<div class="list_item__block_buttons">
						<BaseButton
							:color="'gray'"
							@click="openClientTemplates(item)"
						>
							Шаблоны
						</BaseButton>
						<BaseButton
							:color="'gray'"
							@click="openClientsReports(item)"
						>
							Отчеты
						</BaseButton>
						<BaseButton
							:color="'gray'"
							@click="toggleUserActivation(item)"
						>
							<i :class="getIcon(item.is_active)" />
						</BaseButton>
					</div>
				</div>
			</template>
		</v-virtual-scroll>
		<vue-element-loading 
			:active="isLoading"
			:background-color="'rgba(255, 255, 255, 1)'"
			:color="'blue'"
		/>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import { HTTP } from '@/helpers/httpCommon';
import BaseButton from '@/components/BaseButton.vue';
import BaseSearchBar from '@/components/BaseSearchBar.vue';
export default {
	components: {
		BaseButton,
		BaseSearchBar
	},
	data: () => ({
		clientsOrigin: [],
		isLoading: false,
		modal: false
	}),
	computed: {
		...mapGetters(['getClients']),
		clients() {
			return this.getClients;
		},
	},
	watch: {
		clients(newClients) {
			this.clientsOrigin = [...newClients];
		}
	},
	created() {
		HTTP.get('/users')
			.then(response => {
				this.$store.commit('setClient', response.data.Data.reverse());
			})
			.catch((error) => {
				this.$toast.error(error.message, {
					timeout: 2000
				});
			});
	},
	methods: {
		onFilterHandle(results) {
			this.clientsOrigin = results;
		},
		getColor(name) {
			const colors = [
				'#F46A5F', '#FC8256', '#FFC33D', '#49BEB7',
				'#4C96D7', '#8555D7', '#FF7DCC', '#D4C5F9',
				'#4992E6', '#FF6B8A', '#01A9DB', '#B6FCD5'
			];

			let hash = 0;
			for (let i = 0; i < name.length; i++) {
				hash = name.charCodeAt(i) + ((hash << 5) - hash);
			}

			const index = Math.abs(hash) % colors.length;
			return colors[index];
		},
		getFirstLetter(str) {
			if (str && str.length > 0) {
				return str.charAt(0);
			}
			return '';
		},
		openClientTemplates(item) {
			this.$store.commit('setActiveDashboardCategory', 'user');
			this.$router.push({
				name: 'clientsTemplates',
				params: { userId: item.username, user: item },
			});
		},
		openClientsReports(item) {
			this.$store.commit('setActiveDashboardCategory', 'user');
			this.$router.push({
				name: 'clientsReports',
				params: { userId: item.username, user: item },
			});
		},
		getIcon(active) {
			return active ? 'fa-solid fa-ban red_font' : 'fa-regular fa-circle-check green_font';
		},
		toggleUserActivation(item) {
			let action = null;
			let message = '';
			if(item.is_active) {
				message = 'Клиент деактивирован';
				action = 'deactivate';
			} else {
				message = 'Клиент активирован';
				action = 'activate';
			}
			this.isLoading = true;
			HTTP.get(`user/${action}/${item.username}`)
				.then(() => {
					item.is_active = !item.is_active;
					this.$toast.success(message, {
						timeout: 2000
					});
				})
				.catch(error => {
					this.$toast.error(error.message, {
						timeout: 2000
					});
				})
				.finally(() => {
					this.isLoading = false;
				});
		}
	},
};
</script>

<style lang="scss" scoped>
	.wrapper {
		padding-top: 10px;
		padding-left: 10px;
		padding-right: 0;
		padding-bottom: 0;
	}
	.search-bar {
		margin: 0 30px 0 10px;
	}
	.list_item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-right: 10px;
		height: 70px;
		.block_info {
			display: flex;
			&__picture_frame {
				display: flex;
				justify-content: center;
				align-items: center;
				
				width: 40px;
				height: 40px;

				border-radius: 50%;

				background-color: gray;
				color: white;
			}
			&__name {
				margin-left: 15px;
				align-self: center;
			}
		}
		&__block_info {
			display: flex;
		}
		&__block_buttons {
			display: flex;
			justify-content: space-between;
			min-width: 105px;
		}

		.red_font {
			color: red;
		}
		.green_font {
			color: green;
		}
	}
</style>